
import XHeader from 'vux/src/components/x-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'


export default {
name: 'JoinOrderDetail',
components: {
XHeader,
XCircle
},
created() {
this.getData();
},
data() {
return {
openLevelName:["完全公开","截止后公开","仅跟单人可看","完全保密"],
buyNum:"",
info: {},
trackInfo: {},
joinUserInfo: {},
data1: true,
data2: true,
data3: true,
data4: false
};
},
methods: {
async getData() {
this.$vux.loading.show({
text: 'Loading'
});
console.log(this.$route.params.id);

let res = await this.$http('/queryJoinOrderDetailInfo', {
body: {
joinOrderId: this.$route.params.id
}
});
if (res.returnCode == '0000') {
this.info = Object.assign({}, res.returnMap.joinOrderDetai);
this.trackInfo = Object.assign({}, res.returnMap.trackIssue);
this.joinUserInfo = Object.assign({}, res.returnMap.joinOrderUserInfo);
console.log(this.joinUserInfo);

} else {
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
this.$vux.loading.hide();
},
async buyJoinOrder() {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/buyJoinOrder', {
body: {
joinOrderId: this.$route.params.id,
buyNum:this.buyNum
}
});
if (res.returnCode == '0000') {
this.$vux.toast.show({
text: '下单成功',
type: 'success'
});
this.buyNum="";
this.getData();
} else {
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
this.$vux.loading.hide();
}
}
};



